export class Work{
  private _business : string;
  private _position : string;
  private _starDate : string;
  private _endDate : string;
  private _description : string;
  private _item : number;


  constructor(business: string, position: string, starDate: string, endDate: string, description: string, item: number) {
    this._business = business;
    this._position = position;
    this._starDate = starDate;
    this._endDate = endDate;
    this._description = description;
    this._item = item;
  }

  dateWork() : string{
    return this._starDate + " - " + this._endDate ;
  }

  get business(): string {
    return this._business;
  }

  set business(value: string) {
    this._business = value;
  }

  get position(): string {
    return this._position;
  }

  set position(value: string) {
    this._position = value;
  }

  get starDate(): string {
    return this._starDate;
  }

  set starDate(value: string) {
    this._starDate = value;
  }

  get endDate(): string {
    return this._endDate;
  }

  set endDate(value: string) {
    this._endDate = value;
  }

  get description(): string {
    return this._description;
  }

  set description(value: string) {
    this._description = value;
  }

  get item(): number {
    return this._item;
  }

  set item(value: number) {
    this._item = value;
  }
}



