import { Component, OnInit } from '@angular/core';
import {Person} from "../entities/person";
import {Contact} from "../entities/contact";
import {Skill} from "../entities/skill";

@Component({
  selector: 'app-navbar',
  templateUrl: 'navbar.component.html',
  styleUrls: ['navbar.component.scss']
})

export class NavbarComponent implements OnInit {
  person : Person;
  listContact : Array<Contact>;
  listEmail : Array<Contact>;
  listSkill : Array<Skill>;

  constructor() {
    this.person = new Person("Arturo", "Herrera");
    this.person.occupation = "Arquitecto de Software - Desarrollo";
    this.person.description = "Ingeniero de sistemas, apasionado del analisis y desarrollo de Software";
    this.person.citeCountry = "Santa Cruz - Bolivia";
    this.person.address = "Urb. Guapuru II , C. Canaveral, N 17";

    this.listContact = new Array<Contact>();
    this.listContact.push(new Contact("Personal", "(591) 70856450"));
    this.listContact.push(new Contact("Trabajo", "(591) 71475146"));

    this.listEmail = new Array<Contact>();
    this.listEmail.push(new Contact("Personal", "arturoherreraocana@gmail.com"));

    this.listSkill = new Array<Skill>();
    this.listSkill.push(new Skill("JavaEE (EJB, JPA, EAP, Primefaces)","95%"));
    this.listSkill.push(new Skill("Android Nativo","80%"));
    this.listSkill.push(new Skill("Sql (SqlServer , PostgresSql, Oracle, SQLite)","85%"));
    this.listSkill.push(new Skill("Python (Djanngo)","60%"));
    this.listSkill.push(new Skill("AngularJs, Angular2, Ionic2","65%"));
    this.listSkill.push(new Skill("HTML","75%"));
    this.listSkill.push(new Skill("SASS-CSS","65%"));

  }

  ngOnInit() {
  }

}



