import {Component, OnInit} from '@angular/core';
import {Work} from "../shared/entities/work";
import {Education} from "../shared/entities/education";

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit{

  listWork : Array<Work>;
  listEducation : Array<Education>;

  constructor() {
    this.listWork = new Array<Work>();
    this.listWork.push(new Work("MC4", "Desarrollo Software", "Marzo 2017", "Actual", "Desarrollo de Software, JavaEE, SMS, USSD", 2));
    this.listWork.push(new Work("Humans Software Factory", "Analista , Desarrollo", "Noviembre de 2015", "Febrero de 2017",
      "Desarrollo de software (Web JavaEE)."  +
    "Tottus Billing.- Aplicación web para la emisión de facturas según la norma SFV-14, incluida los módulos de compra, venta e inventario, multi sucursal."
     + " Contabilidad para Consultoras.- Aplicación Web para consultoras contables, genera la contabilidad de múltiples empresas, mediante el ingreso de comprobantes manuales o automáticos."
     + "Historias Clínicas (Centro de Salud Ocupacional SOI).- Aplicación Web para la gestión de historias clínicas de los pacientes del centro de salud ocupacional.", 3));
    this.listWork.push(new Work("Qbit SRL", "Desarrollo Software", "Febrero de 2015", "Octubre 2015",
      "Desarrolo de aplicaciones moviles android nativo, desarrollo de aplicaciones web JEE."
     + "Taxi Corp.- Aplicación para el pedido de radio móvil mediante una aplicación móvil Android para el cliente, Aplicación web para la gestión de pedidos de móviles y control de vehículos de la empresa, aplicación móvil para el chofer para recibir las llamadas y control de posiciones médiate GPS ."
      + "Medifact.- Aplicación Web para la empresa MEDIPIEL SRL., control de inventario, producción de farmacéuticos, control de ventas, gestión de cajas."
      + "Inesco Tiket.- Aplicación Web para la empresa INESCO SA., emisión de ticket de pago a servicios de transporte que solicita la empresa, control de pago e integración con su sistema ERP de la empresa.", 4));
    this.listWork.push(new Work("UAGRM", "Encargado Laboratorio", "Enero 2014", "Diciembre 2014",
      "Encargado de los laboratorios de computo de la Facultad de Ingeniería de Ciencias de La Computación y Telecomunicaciones de la UAGRM, colaboración en departamento de investigación."
    +  "Encargado de Equipos de Computación.- Mantenimiento de los equipos."
     + " Encargado de la Red.- Configuración control y monitoreo de la red de los laboratorios de computación.", 5));

    this.listEducation = new Array<Education>();

    this.listEducation.push(new Education("School Of Engineering", "Diplomado Gerencia de Proyectos de Desarrollo de Software",
      "Junio 2014", "Diciembre 2014", ""));

    this.listEducation.push(new Education("Universidad Autónoma Gabriel René Moreno", "Ingeniería de Sistemas",
      "Marzo 2010", "Junio 2014", ""));

    this.listEducation.push(new Education("UE. Renata Preto de Brunelli", "Bachiller en Humanidades",
      "Febrero 2006", "Diciembre 2009", ""));
  }

  ngOnInit(): void {
  }

}



