export class Education {
  private _institution : string ;
  private _academicDegree : string;
  private _startDate : string;
  private _endDate : string;
  private _description : string;


  constructor(institution: string, academicDegree: string, startDate: string, endDate: string, description: string) {
    this._institution = institution;
    this._academicDegree = academicDegree;
    this._startDate = startDate;
    this._endDate = endDate;
    this._description = description;
  }

  dateEducation() : string{
    return this._startDate + " - " + this._endDate ;
  }

  get institution(): string {
    return this._institution;
  }

  set institution(value: string) {
    this._institution = value;
  }

  get academicDegree(): string {
    return this._academicDegree;
  }

  set academicDegree(value: string) {
    this._academicDegree = value;
  }

  get startDate(): string {
    return this._startDate;
  }

  set startDate(value: string) {
    this._startDate = value;
  }

  get endDate(): string {
    return this._endDate;
  }

  set endDate(value: string) {
    this._endDate = value;
  }

  get description(): string {
    return this._description;
  }

  set description(value: string) {
    this._description = value;
  }
}



