export class Contact {
  private _name : string;
  private _value : string;
  private _icon : string;


  constructor(name: string, value: string) {
    this._name = name;
    this._value = value;
    this._icon = "";
  }

  get icon(): string {
    return this._icon;
  }

  set icon(value: string) {
    this._icon = value;
  }
  get value(): string {
    return this._value;
  }

  set value(value: string) {
    this._value = value;
  }
  get name(): string {
    return this._name;
  }

  set name(value: string) {
    this._name = value;
  }

}



